import React from 'react';

const GridRowComponent = ({verticalCellBorder, children}) => {

  let rowClass = 'grid-component-row grid-component-row-body';

  if (verticalCellBorder) {
    rowClass += ' grid-component-row-vertical-border';
  }

  return (
    <div className={rowClass}>
      {children}
    </div>
  );
};
GridRowComponent.displayName = 'AdslotUiGridRowComponent';

export default GridRowComponent;

GridRowComponent.propTypes = {
  verticalCellBorder: React.PropTypes.bool,
};

GridRowComponent.getDefaultProps = {
  verticalCellBorder: false,
}



