import React from 'react';

const GridHeaderComponent = ({verticalCellBorder, children}) => {

  let rowClass = 'grid-component-row grid-component-row-header';

  if (verticalCellBorder) {
    rowClass += ' grid-component-row-vertical-border';
  }

  return (
    <div className={rowClass}>
      {children}
    </div>
  );
};
GridHeaderComponent.displayName = 'AdslotUiGridHeaderComponent';

export default GridHeaderComponent;

GridHeaderComponent.propTypes = {
  verticalCellBorder: React.PropTypes.bool,
};

GridHeaderComponent.getDefaultProps = {
  verticalCellBorder: false,
}



