require('bootstrap-sass!../../bootstrap-sass.config.js');

import React from 'react';
import {Button, Nav, NavItem} from 'react-bootstrap';
import Grid from './Grid'
import GridRow from './GridRow'
import GridHeader from './GridHeader'
import GridCell from './GridCell'

class AppComponent extends React.Component {
  render() {
    return (
      <div className="index">
        <h1>Buttons</h1>
        <div className="example-component-panel">
          <Button className="btn-inverse">
            Default
          </Button>
          <Button className="btn-inverse" bsStyle="primary">
            Primary
          </Button>
          <Button className="btn-inverse" bsStyle="success">
            Success
          </Button>

        </div>
        <div className="example-component-panel">
          <Button>
            Default
          </Button>
          <Button bsStyle="primary">
            Primary
          </Button>
          <Button bsStyle="success">
            Success
          </Button>
        </div>
        <div className="example-component-panel">
          <Button bsSize="xsmall">
            Default
          </Button>
          <Button bsSize="xsmall" bsStyle="primary">
            Primary
          </Button>
          <Button bsSize="xsmall" bsStyle="success">
            Success
          </Button>
        </div>
        <h1>Tabs</h1>
        <div className="example-component-panel">
          <Nav bsStyle="tabs">
            <NavItem active={true}>Targeting</NavItem>
            <NavItem>Audience</NavItem>
            <NavItem>Billing</NavItem>
          </Nav>
        </div>
        <h1>Grid</h1>
        <div className="example-component-panel">
          <Grid>
            <GridHeader>
              <GridCell>
                Header
              </GridCell>
              <GridCell>
                Header
              </GridCell>
              <GridCell>
                Header
              </GridCell>
            </GridHeader>
            <GridRow verticalCellBorder={true}>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
            </GridRow>
            <GridRow>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
            </GridRow>
            <GridRow>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
              <GridCell>
                Content
              </GridCell>
            </GridRow>
          </Grid>
        </div>
      </div>
    );
  }
}

export default AppComponent;



