import React from 'react';

require('styles/components/Grid.scss');

const GridComponent = ({children}) => {

  return (
    <div className='grid-component'>
      {children}
    </div>
  );
};

GridComponent.displayName = 'AdslotUiGridComponent';

export default GridComponent;




